<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="data_sift">
                    <div class='data_s_t'>
                        <span>数据筛选</span>
                    </div>
                    <el-row type='flex' justify="space-around">
                        <el-col :span='5'>
                            <div class="data_s">
                                <el-row>
                                    <el-col :span='7'>IP地址：</el-col>
                                    <el-col :span='17'>
                                        <el-input v-model='IP' @input='select'></el-input>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s">
                                <el-row>
                                    <el-col :span='7'>操作内容：</el-col>
                                    <el-col :span='17'>
                                        <el-input v-model='text' @input='select'></el-input>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='6'>
                            <div class="data_s">
                                <el-row>
                                    <el-col :span='7'>时间范围：</el-col>
                                    <el-col :span='17'>
                                        <el-date-picker style='width:100%;font-size:12px;' v-model="time" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
                                        </el-date-picker>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s">
                                <el-row>
                                    <el-col :span='8'>
                                        <el-button type="primary" @click='select' icon="el-icon-search">查询</el-button>
                                    </el-col>
                                    <el-col :span='8' :offset='2'>
                                        <!-- <el-button @click='reset' icon='el-icon-refresh'>重置</el-button> -->
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="data_list">
                    <div class="data_title">
                        <span>数据列表</span>
                    </div>
                    <div>
                        <el-table :data="list_log.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%" :header-cell-style="rowClass" :cell-style="cellStyle" border>
                            <el-table-column prop="operationId" label="序号" width="180">
                            </el-table-column>
                            <el-table-column prop="operationIp" label="IP地址">
                            </el-table-column>
                            <!-- <el-table-column
    					    prop=""
    					    label="操作人员">
    					  </el-table-column> -->
                            <el-table-column prop="operationContent" label="操作内容">
                            </el-table-column>
                            <el-table-column prop="operationTime" label="操作时间">
                            </el-table-column>
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="total">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            currentPage: 1,
            pagesize: 20,
            title: '操作日志',
            total: 0,
            list_log: [],
            IP: '',
            text: '',
            time: '',
            pickerOptions: {
                shortcuts: [{
                    text: '最近一周',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近一个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近三个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }
        }
    },
    methods: {
        formatDate(date) {
            if (typeof date == "string") {
                return this.formatDate2(date);
            }
            var y = date.getFullYear();
            var M = date.getMonth() + 1;
            M = M < 10 ? "0" + M : M;
            var d = date.getDate();
            d = d < 10 ? "0" + d : d;
            var h = date.getHours();
            h = h < 10 ? "0" + h : h;
            var m = date.getMinutes();
            m = m < 10 ? "0" + m : m;
            var s = date.getSeconds();
            s = s < 10 ? "0" + s : s;
            return y + "-" + M + "-" + d + " " + h + "-" + m + "-" + s;
        },
        reset() {
            this.text = '',
                this.IP = '',
                this.time = '',
                this.select()
        },
        select() {
            var a;
            var b;
            if (this.time == '') {
                a = '';
                b = '';
            } else {
                a = this.formatDate(this.time[0])
                b = this.formatDate(this.time[1])
            }
            this.$axios.post('/tomlive/operation/selectOperationByCondition', this.$qs.stringify({
                operationContent: this.text,
                operationIp: this.IP,
                beginTime: a,
                endTime: b
            })).then((res) => {
                console.log(res)
                this.list_log = res.data.info
                this.total = this.list_log.length
            })
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#f5f5f5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        }
    },
    mounted() {
        this.$axios.post('/tomlive/operation/selectAllOperation').then((res) => {
            this.list_log = res.data.info.list
            console.log(res)
            this.total = res.data.info.total
        })
    }
}
</script>
<style scoped>
.data_sift {
    width: 100%;
    height: 132px;
    border: 1px solid #e9e9e9;
    background: #fff;
    margin: 20px 0 0 0;
    box-sizing: border-box;
}

.data_s_t {
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.data_s_t>span {
    margin: 0 0 0 20px;
    font-size: 14px;
    line-height: 50px;
    font-weight: bold;
    color: #666;
}

.data_s {
    width: 100%;
    height: 35px;
    line-height: 35px;
    margin: 20px 0 0 0;
    font-size: 14px;
    color: #666;
}

.data_list {
    width: 100%;
    padding: 0 20px;
    min-height: 800px;
    background: #fff;
    margin: 20px 0;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.data_title {
    width: 100%;
    height: 60px;
    font-size: 14px;
    line-height: 60px;
    color: #666;
    font-weight: bold;
}

.pages {
    margin: 20px 0 20px 0;
    float: right;
}
</style>